import React, { useState } from 'react';
import { Menu, MenuItem, CircularProgress, Typography, Chip } from '@material-ui/core';
import makeStyles from '@material-ui/styles/makeStyles';
import { Link as DomLink, useHistory } from 'react-router-dom';
import { Add as AddIcon, CloudUpload as UploadIcon } from '@material-ui/icons';
import GoogleMap from './GoogleMap';
import CSVUploadModal from './CSVUploadModal';
import DeleteModal from './DeleteModal';
import DropdownMenu from './DropdownMenu';
import GenericPage from './GenericPage';
import FormStepper from './FormStepper';
import FieldsWrapper from "./FieldsWrapper";
import OtherLocationsFields from "./OtherLocationsFields";

import { providerFormTypes } from '../../constants/provider_fields.js'

const useStyles = makeStyles((theme) => ({
  link: {
    color: 'inherit',
    textDecorationLine: 'none',
  },
  linkWithHover: {
    fontWeight: 500,
    color: 'inherit',
    // textDecorationLine: 'none',
    '&:hover': {
      textDecorationLine: 'underline',
      color: '#2f5ac7'
    }
  },
  progress: {
    margin: theme.spacing(2),
    marginTop: 50,
  },
  chipButton: {
    paddingLeft: 6,
    marginLeft: 6,
  }
}));

export function Link({className = '', color, ...props}) {
  const classes = useStyles();
  return <DomLink {...props} className={color ? classes.linkWithHover : classes.link + ' ' + className}/>
}

/**
 * Used by DataTable. Add "add button" and "upload by CSV button"
 * @param onClick
 * @returns {*}
 * @constructor
 */
export function CustomToolbar({handleAdd, handleUpload, type}) {
  const history = useHistory();
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLink = link => () => {
    history.push(link);
  };

  return (
    <>
      <Chip onClick={handleUpload}
            color="default"
            icon={<UploadIcon/>}
            label="Upload"
            variant="outlined"
            className={classes.chipButton}
      />
      <Chip onClick={type === 'providers' ? handleClick : handleAdd}
            color="primary"
            icon={<AddIcon/>}
            label="Add"
            variant="outlined"
            className={classes.chipButton}
      />

      {/*For Providers */}
      {type === 'providers' &&
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
      >
        {Object.entries(providerFormTypes).map(([value, formType]) =>
          <MenuItem key={formType} onClick={handleLink(`/providers/${value}/new`)}>
            {formType}
          </MenuItem>)
        }
      </Menu>
      }
    </>
  );
}

export function Loading({message = 'Loading Components...'}) {
  const classes = useStyles();
  return (
    <div style={{textAlign: 'center'}}>
      <CircularProgress className={classes.progress}/>
      <Typography variant="subtitle2" color={"textSecondary"}>
        {message}
      </Typography>
    </div>
  );
}


export {
  GoogleMap, CSVUploadModal, DeleteModal, DropdownMenu, GenericPage, FormStepper, FieldsWrapper,
  OtherLocationsFields
}
export {EnhancedTable as DataTable} from './Table';
